import { TableColumn, TableHeader, TableLastChild } from 'components/table'
import Loader from 'components/table/Loader'
import Pagination from 'components/table/Pagination'
import moment from "moment"
import React, { useEffect, useState } from 'react'
import { blockFeed, getAllFeeds } from 'resources/serverReq/admin/feed'
import SLUGS from 'resources/slugs'
import "../../common/common.css"
import '../../table/table.css'

export default function Feeds() {
    const [feedsData, setFeedData] = useState([])
    const [page, setPage] = useState(1);
    const [query, setQuery] = useState("");
    const [status, setStatus] = useState("")
    const [pageData, setPageData] = useState({});
    const [dataNotAvailable, setDataNotAvailable] = useState(false)
    const [isLoading, setIsLoading] = useState(true)

    useEffect(() => {
        const params = {
            page: page,
            search: query,
            status: status

        };

        getAllFeeds(params).then((response) => {
            setIsLoading(true)
            setDataNotAvailable(false)
            if (response?.data?.code == 200) {
                const res = response?.data?.data.feeds
                setFeedData(res.rows)
                setPageData({ count: res.count, currentPage: res.currentPage, totalPage: res.totalPage })
                if (res.count == 0) setDataNotAvailable(true)

            }
            setIsLoading(false)
        }).catch((err) => {
            console.log(err)
            setIsLoading(false)
            setDataNotAvailable(false)
        })

    }, [query, status, page])

    async function statusUpdateFeed(id) {
        await blockFeed(id).then((res) => {

            if (res.data.code === 200) {
                console.log(" ~ file: Feeds.js ~ line 47 ~ awaitblockFeed ~ res", res)
                const feedLists = feedsData.slice();
                for (const feed of feedLists) {
                    if (feed.id === id) {
                        feed.status = res?.data?.data?.feed?.status;

                        break;
                    }
                }
                setFeedData(feedLists);
            }
        }).catch(function (error) {
            console.log(error.response);
        });
    }

    return (
        <>
            {

                !isLoading ?
                    <div className="table-responsive">
                        <div className="table-wrapper">

                            <TableHeader btnTitle={"+ Add Feed "} route={SLUGS.createFeed} setQuery={setQuery} query={query} />
                            <div className="form-group w-15">
                                <label className='text-dark'>Status</label>
                                <select className="select-input p-2" onChange={(e) => setStatus(e.target.value)} value={status}>
                                    <option >Select</option>
                                    <option value="1" >Active</option>
                                    <option value="0">Inactive</option>
                                </select>
                            </div>
                            <table className="table table-borderless text-center">
                                <TableColumn columns={["Title", "Short Description", "Type", "Status", "Created On", "Action"]} />

                                <tbody>
                                    {
                                        dataNotAvailable ? <tr><td></td><td></td><td></td><td>No Record Found</td><td></td></tr> :
                                            feedsData.map((el) => (
                                                <tr key={el.id}>
                                                    <td >


                                                        <div className="d-flex flex-row bd-highlight p-2 ">
                                                            {/* <div className="d-flex align-items-start rounded-top">
                                                                <div className="symbol symbol-md bg-light-primary flex-shrink-0">
                                                                    <img className="image-position symbol-round"
                                                                        src={
                                                                            "/assets/img/300_21.jpg"
                                                                        }
                                                                        alt="pic"
                                                                    />
                                                                </div>
                                                            </div> */}
                                                            <span className='p-2'>
                                                                {el.title}

                                                            </span>
                                                        </div>

                                                    </td>
                                                    <td className='feed-description'>{el.shortDescription}</td>
                                                    <td className='feed-description'>{el.type}</td>
                                                    <td>
                                                        {
                                                            el.status == true ?
                                                                <span className='label label-lg label-light-success label-inline'>Active</span> :
                                                                <span className='label label-lg label-light-danger label-inline'>Inactive</span>
                                                        }
                                                    </td>
                                                    <td>{moment(el.createdAt).format('DD-MMM-YY hh:mm a')}</td>
                                                    <td >
                                                        <div className='d-flex'>
                                                            <TableLastChild editRoute={SLUGS.editFeed} editData={el} block={statusUpdateFeed} blockId={el.id} BlockCondition={el.status} path={SLUGS.viewFeed} />

                                                        </div>

                                                    </td>
                                                </tr>
                                            ))}
                                </tbody>
                            </table>
                            <Pagination pageData={pageData} setPage={setPage} />

                        </div>
                    </div> : <Loader />
            }
        </>
    )
}
