import { FormBtns } from "components/table";
import { useFormik } from "formik";
import React, { useState } from 'react';
import { toast } from 'react-toastify';
import { addReferenceCode } from "resources/serverReq/admin/reference-code";
import slugs from 'resources/slugs';
import * as Yup from "yup";
import "../../common/common.css";


const initialValues = {
    description: "",
};

export default function AddReferenceCode(props) {
    const [loading, setLoading] = useState(false);
    const medicineSchema = Yup.object().shape({

        description: Yup.string()
            .min(3, "Minimum 3 symbols")
            .max(50, "Maximum 50 symbols")
            .required("Required"),
    });

    const enableLoading = () => {
        setLoading(true);
    };

    const disableLoading = () => {
        setLoading(false);
    };

    const getInputClasses = (fieldName) => {
        if (formik.touched[fieldName] && formik.errors[fieldName]) {
            return "is-invalid";
        }

        if (formik.touched[fieldName] && !formik.errors[fieldName]) {
            return "is-valid";
        }

        return "";
    };

    const formik = useFormik({
        initialValues,
        validationSchema: medicineSchema,
        onSubmit: (values, { setStatus, setSubmitting }) => {
            enableLoading();
            addReferenceCode(values).then((res) => {
                if (res?.data?.code === 200) {
                    toast.success('Referral code has been successfully added in system.')
                    props.history.push(slugs.referenceCode)
                }
                disableLoading();
            })
                .catch((err) => {
                    disableLoading();
                    setSubmitting(false);
                    const errorMessage = err.response.data.errorMessage;
                    toast.error(errorMessage, {
                        closeOnClick: true,
                        hideProgressBar: false,
                    })
                });
        },
    });

    return (
        <>
            <div className='row'>
                <div className='card col-md-6 offset-md-3'>

                    <h3 className='d-flex justify-content-center align-items-center form-title mb-5'>Add Medicine</h3>
                    <div className="container-xl">
                        <form
                            onSubmit={formik.handleSubmit}

                        >

                            <div className="form-group form-input-2 mb-3">
                                <label>Description</label>
                                <input
                                    placeholder="Referral Code Description"
                                    type="description"
                                    className={`form-control form-control-solid h-45px ${getInputClasses(
                                        "description"
                                    )}`}
                                    name="description"
                                    {...formik.getFieldProps("description")}
                                />
                                {formik.touched.description && formik.errors.description ? (
                                    <div>
                                        <div className="fv-help-block">{formik.errors.description}</div>
                                    </div>
                                ) : null}
                            </div>
                            <FormBtns formik={formik.isSubmitting} cancelRoute={slugs.referenceCode} />

                        </form>
                    </div>
                </div>
            </div>
        </>
    );
}

