import { TableColumn, TableHeader, TableLastChild } from 'components/table'
import Loader from 'components/table/Loader'
import Pagination from 'components/table/Pagination'
import moment from "moment"
import React, { useEffect, useState } from 'react'
import { getAllCompany } from 'resources/serverReq/admin/company'
import { getStates } from "resources/serverReq/common"
import SLUGS from 'resources/slugs'
import { blockUsers, getAllUsers } from "../../../resources/serverReq/admin/user"
import "../../common/common.css"
import '../../table/table.css'
import SelectInput from '../SelectInput'

export default function Doctor() {
    let [userData, setUserData] = useState([]);
    const [perPageRows, setPerPageRows] = useState(7);
    const [page, setPage] = useState(1);
    const [query, setQuery] = useState("");
    const [status, setStatus] = useState("")
    const [state, setState] = useState("")
    const [pageData, setPageData] = useState({});
    const [dataNotAvailable, setDataNotAvailable] = useState(false)
    const [isLoading, setIsLoading] = useState(false)
    const [stateOptions, setStateOptions] = useState([]);
    const [companyId, setCompanyId] = useState(null);
    const [companyOptions, setCompanyOptions] = useState([]);

    const params = {
        page: page,
        role: perPageRows,
        search: query,
        role: "doctor",
        sort: 1,
        status: status,
        state: state,
        company_id: companyId
    };

    useEffect(() => {
        getAllCompany()
            .then(({ data }) => {
                const companies = data.data.companies.rows;
                let options = [];
                companies.map((val) => {
                    options.push({
                        value: val.id,
                        label: val.name
                    });
                });
                setCompanyOptions(options);
            })
            .catch((err) => {
                console.log(" ~ file: Adherence.js ~ line 96 ~ useEffect ~ err", err)
            });

        getStates().then(({ data }) => {
            const states = data.data.states;
            let options = []
            states.map(val => {
                options.push({
                    value: val.name,
                    label: val.name
                })
            });
            setStateOptions(options);
        }).catch(err => {
            console.log(" ~ file: AddPatient.js ~ line 73 ~ useEffect ~ err", err)
        });


    }, [])


    useEffect(() => {

        getAllUsers(params).then((response) => {
            setIsLoading(true)
            setDataNotAvailable(false)
            if (response?.data?.code == 200) {
                const res = response?.data?.data.users
                setPageData({ count: res.count, currentPage: res.currentPage, totalPage: res.totalPage })
                setUserData(res.rows)
                if (res.count == 0) setDataNotAvailable(true)
            }
            setIsLoading(false)

        }).catch((err) => {
            console.log(err)
            setIsLoading(false)
            setDataNotAvailable(false)
        })

    }, [status, query, state, page, companyId])

    async function blockedUser(id) {
        await blockUsers(id).then((res) => {
            if (res.data.code === 200) {
                const userLists = userData.slice();
                for (const user of userLists) {
                    if (user.id === id) {
                        user.status = res?.data?.data?.user?.status;

                        break;
                    }
                }
                setUserData(userLists);
            }
        }).catch(function (error) {
            console.log(error.response);
        });
    }

    return (
        <>
            {
                !isLoading ?
                    <div className="table-responsive">
                        <div className="table-wrapper">
                            <div className="table-title">

                                <TableHeader btnTitle={"+ Add Doctor"} route={SLUGS.addDoctor} setQuery={setQuery} query={query} />
                                <SelectInput stateOptions={stateOptions} state={state} setState={setState} status={status} setStatus={setStatus} companyOptions={companyOptions} companyId={companyId} setCompanyId={setCompanyId} />
                            </div>
                            <table className="table table-borderless text-center">
                                <TableColumn columns={["Name", "Email", "Code", "Mobile", "State", "Status", "Company", "Created On", "Actions"]} />
                                <tbody>
                                    {
                                        dataNotAvailable ? <tr><td></td><td></td><td></td><td></td><td>No Record Found</td><td></td><td></td><td></td><td></td></tr> :
                                            userData.map((el) => (
                                                <tr key={el.id}>
                                                    <td>
                                                        <div className="d-flex flex-row bd-highlight p-2 ">
                                                            {/* <div className="d-flex align-items-start rounded-top">
                                                                <div className="symbol symbol-md bg-light-primary flex-shrink-0">
                                                                    <img className="image-position symbol-round"
                                                                        src={
                                                                            "/assets/img/300_21.jpg"
                                                                        }
                                                                        alt="pic"
                                                                    />
                                                                </div>
                                                            </div> */}
                                                            <span className='p-2'>
                                                                {el.firstName + " " + el.lastName}

                                                            </span>
                                                        </div>

                                                    </td>

                                                    <td>{el.email}</td>
                                                    <td>{el.reference_code}</td>
                                                    <td>{el.phone}</td>
                                                    <td>{el.state}</td>
                                                    <td>
                                                        {
                                                            el.status == true ?
                                                                <span className='label label-lg label-light-success label-inline'>Active</span> :
                                                                <span className='label label-lg label-light-danger label-inline'>Inactive</span>
                                                        }
                                                    </td>
                                                    <td>{el.company_associated}</td>
                                                    <td>{moment(el.createdAt).format('DD-MMM-YY hh:mm a')}</td>
                                                    <td>
                                                        <TableLastChild editRoute={SLUGS.editDoctor} editData={el} block={blockedUser} blockId={el.id} BlockCondition={el.status} path={SLUGS.viewPatientDoctor + "/" + el.id} />
                                                    </td>
                                                </tr>
                                            ))}
                                </tbody>
                            </table>
                            <Pagination pageData={pageData} setPage={setPage} />

                        </div>
                    </div>
                    : <Loader />
            }
        </>
    )
}
